.parent-card {
  position: relative;
  height: 250px;
  width: 190px;
  font-family: "Fira Sans", sans-serif;
}
.memberCard {
  background-color: #121212;
  margin: 0 auto;
  color: white;
  padding: 25px;
  border: solid 1px gray;
  text-align: center;
  border-radius: 5px;
  border-width: thin;
  cursor: pointer;
  height: 250px;
  width: 190px;
  transition: 0.5s ease;
  opacity: 1;
}

.profile-card {
  display: flex;
  flex-direction: column;
}

.contact-card {
  opacity: 0;
  transition: 1s ease;
  background-color: black;
  margin: 0 auto;
  border: solid 1px gray;
  border-radius: 5px;
  border-width: thin;
  cursor: pointer;
  height: 250px;
  width: 190px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-bottom {
  margin-top: 10px;
}

#member-image {
  max-width: 100%;
  object-fit: contain;
  padding-bottom: 0;
  border-radius: 50%;
}

.member-name {
  font-size: 16px;
}

.member-role {
  margin-top: 5px;
  color: gray;
  font-size: 13px;
}

.parent-card:hover .profile-card {
  opacity: 0.2;
}

.parent-card:hover .contact-card {
  opacity: 0.6;
}

.Icon {
  width: 24px;
  height: 24px;
  fill: white;
  margin: 6px;
}

.disabled {
  width: 24px;
  height: 24px;
  fill: gray;
  margin: 6px;
  cursor: not-allowed;
  pointer-events: none;
}

.contactLink {
  width: 36px;
  height: 36px;
}

@media screen and (max-width: 664px) {
  .parent-card {
    height: 125px;
    width: 95px;
  }
  .memberCard {
    height: 125px;
    width: 95px;
    padding: 12.5px;
    border-radius: 2.5px;
  }
  .contact-card {
    height: 125px;
    width: 95px;
    border-radius: 2.5px;
  }
  .card-bottom {
    margin-top: 5px;
  }
  .member-name {
    font-size: 8px;
  }
  .member-role {
    margin-top: 2.5px;
    color: gray;
    font-size: 6.5px;
  }
  .Icon {
    width: 12px;
    height: 12px;
    fill: white;
    margin: 3px;
  }

  .disabled {
    width: 12px;
    height: 12px;
    fill: gray;
    margin: 3px;
    cursor: not-allowed;
    pointer-events: none;
  }

  .contactLink {
    width: 18px;
    height: 18px;
  }
}
